<template>
  <el-card class="box-card">
  <div slot="header" class="clearfix">
    <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
    <el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
    </el-breadcrumb>
  </div>
      <el-form ref="form" :model="article" :rules="rules" label-width="60px">
  <el-form-item label="" prop="title">
    <el-input v-model="article.title"></el-input>
  </el-form-item>
    <el-form-item label="" prop="content">
    <el-tiptap
       lang="zh"
       v-model="article.content"
       :extensions="extensions"
       height="350"
       placeholder=""
     ></el-tiptap>
  </el-form-item>
  <el-form-item label="">
    <el-radio-group v-model="article.cover.type">
      <el-radio :label="1"></el-radio>
      <el-radio :label="3"></el-radio>
      <el-radio :label="0"></el-radio>
      <el-radio :label="-1"></el-radio>
    </el-radio-group>
    <template v-if="article.cover.type > 0">
      <upload-cover
      :key="cover"
      v-for="(cover, index) in article.cover.type"
      v-model="article.cover.images[index]"
    ></upload-cover>
    </template>
  </el-form-item>
    <el-form-item label="" prop="channel_id">
    <el-select v-model="article.channel_id" placeholder="">
      <el-option
        :label="(item.name)"
        v-for="(item, index) in channels"
        :key="index"
        :value="(item.id)">
      </el-option>
    </el-select>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onPunlish (false)"></el-button>
    <el-button></el-button>
  </el-form-item>
</el-form>
</el-card>
</template>

<script>
import UploadCover from './components/upload-cover.vue'

import {
  getChannels,
  addArticle,
  // updateArticle,
  getArticle
} from '@/api/article'

import {
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Image,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  TodoItem,
  TodoList,
  HorizontalRule,
  Fullscreen,
  Preview,
  CodeBlock,
  TextColor
} from 'element-tiptap'

import 'element-tiptap/lib/index.css'

import { uploadImage } from '@/api/images'

export default {
  name: 'PublishIndex',
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  },
  props: {},
  data () {
    return {
      channels: [],
      article: {
        title: '',
        content: '',
        cover: {
          type: 1,
          images: []
        },
        channel_id: null
      },
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 3 }),
        new Bold({ bubble: true }), // 
        new Underline(), // 
        new Italic(), // 
        new Image({
          //  base64 
          uploadRequest (file) {
            //  Content-Type  multipart/form-data FormData
            const fd = new FormData()
            fd.append('image', file)
            // 1 return  Promise 
            //  axios  Promise 
            return uploadImage(fd).then(res => {
              //  return 
              return res.data.data.url
            })
          } //  Promise<url>
        }),
        new Strike(), // 
        new HorizontalRule(), // 
        new ListItem(),
        new BulletList(), // 
        new OrderedList(), // 
        new TodoItem(),
        new TodoList(),
        new Fullscreen(),
        new Preview(),
        new CodeBlock(),
        new TextColor()
      ],
      rules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        content: [
          // { required: true, message: '', trigger: 'change' }
          {
            validator (rule, value, callback) {
              console.log('content validator')
              if (value === '<p></p>') {
                // 
                callback(new Error(''))
              } else {
                // 
                callback()
              }
            }
          },
          { required: true, message: '', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '' }
        ]
      }
    }
  },
  computed: {
  },
  watch: {},
  created () {
    this.loadChannels()
    // if (this.$route.query.id) {
    //   this.loadArticle
    // }
    this.loadArticle()
  },
  mouted () {},
  methods: {
    loadChannels () {
      getChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    onPunlish () {
      addArticle(this.article).then(res => {
        this.$message({
          type: 'success',
          message: '!'
        })
      })
    },
    loadArticle () {
      getArticle(this.$route.query.id).then(res => {
        this.article = res.data.data
      })
    },
    onUpdateCover (index, url) {
      this.article.cover.images[index] = url
    }
  }
}
</script>

